body {
  font-family: 'Source Sans Pro', sans-serif;
}

ul {
  list-style: none;
  margin: 0;
  padding: 0;
}

img {
  pointer-events: none!important;
}

a {
  color: inherit;
  text-decoration: none;
}

h1 {
  font-size: 30px;

}

h2 {
  font-size: 28px;
  line-height: 1.18;
  text-transform: uppercase;
  text-align: center;
  font-weight: 700;
  font-family: 'Source Sans Pro', sans-serif;


  @include media('>tablet') {
    font-size: 36px;
  }

}

h3 {
  font-size: 28px;
  line-height: 1.0714;
  color: #434343;
  margin: 20px 0;
  font-weight: 600;
  letter-spacing: 0.4px;
  font-family: 'Source Sans Pro', sans-serif;

  @include media('>tablet') {
    margin:  0 0 20px;
  }
}

h5 {
  font-size: 18px;
  text-transform: uppercase;
  text-align: center;
  font-weight: 600;
  font-family: 'Source Sans Pro', sans-serif;
}

address {
  font-style: normal;
}

p {
  letter-spacing: 0.4px;
}

.slick-dots {
  bottom: 30px;

  li {
    border-radius: 50%;
    background: #979797;
    width: 10px;
    height: 10px;
    margin: 0 10px;

    &.slick-active {
      background: #FFA300;
    }
  }
}

.slick-prev, .slick-next {
  z-index: 2;
  &:before {
    display: none;
  }
}

.slick-prev {
  left: 5vw;

  @include media('>tablet') {
    left: 17vw;
  }
}

.slick-next {
  right: 5vw;

  @include media('>tablet') {
    right: 17vw;
  }
}

.wrapper {
  position: relative;
  overflow: hidden;
  width: 100%;

  &.open-menu {
    position: fixed;
  }
}

.btn {
  width: 235px;
  height: 50px;
  background: #20D07F;
  color: #fff;
  font-size: 17px;
  font-weight: 700;
  letter-spacing: 0.7px;
  border-radius: 25px;
  display: flex;
  align-items: center;
  justify-content: center;
  border: none;
  outline: none;
  padding: 10px;
  box-shadow: 0px 31px 35px -12px #13C171;
  transition: background 0.3s;

  &:hover {
    background: #13C171;
  }
}

.container {
  padding: 0 15px;
  max-width: 1054px;
  width: 100%;
  margin: 0 auto;

}

.col {
  width: 100%;

  @include media('>tablet') {
    width: 50%;
  }
}

.caption {
  width: 100%;
  text-align: center;
  margin:  0 0 45px;

  @include media('>tablet') {
    margin:  0 0 75px;
  }

  p {
    font-size: 18px;
    line-height: 1.27;
  }
}

.slider {
  /*min-height: 700px;*/
  width: 100%;

  .slide {
    position: relative;
    h1 {
      position: absolute;
      left: 0;
      top: 10%;
      opacity: 0.5;
      color: #fff;
      font-size: 14vw;
      font-family: 'SignPainter HouseScript';
      width: 100%;
      text-align: center;
      /*letter-spacing: 1px;*/
      font-weight: 400;
    }
    img {
      width: 100%;
      max-height: 220px;

      @include media('>phone') {
        max-height: 450px;
      }

      @include media('>tablet') {
        max-height: 700px;
      }
    }
  }
}

.slider-section {
  position: relative;
  padding: 0 0 45px;

  @include media('>tablet') {
    padding: 0 0 55px;
  }

  &:before {
    content: '';
    position: absolute;
    left: 50%;
    bottom: 0;
    transform: translateX(-50%);
    border-radius: 50%;
    height: 4px;
    width: 100%;
    background-image: url("../images/Rectangle@3x.svg");
    background-repeat: no-repeat;
    background-position: center;
    background-size: contain;
    //background: linear-gradient(to right, rgba(255,175,75,0.7) 0%, rgba(255,146,10,1) 39%, rgba(255,146,10,1) 50%, rgba(255,146,10,1) 62%, rgba(255,146,10,1) 63%, rgba(255,146,10,0.58) 100%);

  }
  .about-text {
    max-width: 1000px;
    width: 100%;
    margin: 0 auto;
    padding: 35px 0 0 0;

    @include media('>tablet') {
      padding: 35px 0 0 0;
    }

    strong {
      font-weight: 700;
    }

    span {
      font-weight: 200;
    }

    p {
      font-size: 18px;
      line-height: 1.5;
      text-align: center;
      color: #3E3E3E;
      @include media('>tablet') {
        font-size: 21px;
        line-height: 2.14;
      }
    }
  }
}

.section-offer {
  background: #F7F7F7;
  padding: 45px 0 0;

  @include media('>tablet') {
    padding: 90px 0 25px;
  }

  .caption {
    margin: 0 0 95px;
  }

  .offer-items {
    padding: 0 0 45px;

    @include media('>tablet') {
      padding: 0 0 80px;
    }

    p {
      /*letter-spacing: 1px;*/
      padding: 0;
      @include media('>tablet') {
        padding: 0 60px 0 0;
      }
    }

    &:nth-child(even) {
      .holder {
        flex-direction: row-reverse;
        p {
          padding: 0;
          @include media('>tablet') {
            padding: 0 60px 0 0;
          }
        }
      }
    }

    .holder {
      display: flex;
      align-items: center;
      font-size: 18px;
      color: #3E3E3E;
      line-height: 1.666;
      flex-wrap: wrap;
    }

    .col {
      text-align: center;
      @include media('>tablet') {
        text-align: left;
      }
    }

  img {
    @include media('<tablet') {
      width: 60%;
    }
  }

    .custom-style {
      img {
        @include media('<tablet') {
          width: 70%;
        }
      }

    }


  }
}

.gallery-section {
  padding:45px 0;

  @include media('>tablet') {
    padding: 88px 0 95px;
  }

  .gallery-holder {
    display: flex;
    flex-wrap: wrap;
    margin:  0 -10px;
  }

  .gallery-img {
    width: 80%;
    padding: 10px;
    margin:  0 auto;

    @include media('>tablet') {
      width: 100%;
    }

    .img-holder {
      //overflow: hidden;
    }

    img {
      transition: transform 0.3s;
      pointer-events: none;
    }

    &:hover {
      img {
        transform: scale(1.1);
      }
    }

    @include media('>phone') {
      width: 50%;
    }

    @include media('>tablet') {
      width: 33.332%;
    }

    img {
      width: 100%;
    }
  }
}

.price-section {
  background: #F7F7F7;
  padding: 45px 0;

  @include media('>tablet') {
    padding: 100px 0;
  }

  h5 {
    font-size: 16px;
    color: #9AA4AC;
    text-transform: uppercase;
  }

  .price-list {
    display: flex;
    flex-wrap: wrap;
    padding: 0 0 45px;

    @include media('>tablet') {
      padding: 0 0 40px;
    }

    .price-item {
      width: 100%;
      padding: 10px;

      @include media('>phone') {
        width: 50%;
      }

      @include media('>tablet') {
        width: 25%;
      }

      strong {
        font-size: 24px;
        line-height: 1.28;
        padding: 0 0 15px;
        display: block;
        font-weight: 600;
        colo: #353434;

        @include media('>phone') {
          font-size: 30px;
          padding: 0 0 30px;
        }
      }

      .holder {
        background: #fff;
        padding: 30px;
        border-radius: 10px;
        height: 100%;
        border: 1px solid #E4E7EC;
        transition: box-shadow 0.3s;
        &:hover {
          box-shadow: 0px 11px 35px 6px rgba(0,0,0,0.10);
        }

        @include media('>tablet') {
          padding: 35px;
        }
      }
    }
  }

  .price-detail {
    width: 160px;
    margin: 0 auto;
    @include media('>tablet') {
      width: 100%;
    }

  }

  .price {
    font-size: 26px;
    color: #353434;
    text-align: center;
    font-weight: 600;

    @include media('>tablet') {
      font-size: 30px;
    }

  }

  .color-dotes {
    position: relative;
    font-size: 15px;
    font-weight: 400;
    line-height: 1.28;
    color: #888E93;
    margin: 0 0 15px;

    &:before {
      content: '';
      position: absolute;
      left: -16px;
      top: 50%;
      transform: translateY(-50%);
      width: 6px;
      height: 6px;
      border-radius: 50%;
    }
  }

  .yellow-dots {
    &:before {
      background: #FFB50D;
    }
  }

  .orange-dots {
    &:before {
      background: #F26F08;
    }
  }

  .blue-dots {
    &:before {
      background: #7ED3F0;
    }
  }

   .violet-dots {
     &:before {
       background: #D067A4;
     }
   }

  .pink-dots {
    &:before {
      background: #FF70B8;
    }
  }

  .btn {
    margin: 0 auto;
  }
}

.contacts-section {
  padding: 45px 0 0;

  @include media('>tablet') {
    padding: 100px 0 0;
  }

  .caption {
    margin: 0 0 35px;
  }

  .contact-holder {
    max-width: 720px;
    width: 100%;
    margin: 0 auto;
    padding: 0 0 45px;

    @include media('>tablet') {
      padding: 0 0 85px;
    }
  }

  .col-holder {
    display: flex;
    flex-wrap: wrap;
    padding: 0;
    flex-direction: column-reverse;
    @include media('>phone') {
      padding: 0 0 35px;
      flex-direction: row;
    }

    .col {
      width: 100%;
      padding: 20px 0 0 0;

      @include media('>phone') {
        width: 50%;
        padding: 20px 0 0 0;
      }
    }

  }

  .social_list {
    flex-wrap: wrap;
    max-width: 200px;
    justify-content: center;
    margin: 40px auto 20px;
    @include media('>phone') {
      margin: 0;
      justify-content: center;
      max-width: 210px;
      /*justify-content: flex-end;*/
    }

    li {
      padding: 5px 12px;
    }


    a {
      opacity: 1;
      transition: opacity 0.3s;
      &:hover {
        opacity: 0.5;
      }
    }

    img {
      width: 25px;
    }

    .phone {
      flex-grow: 1;
      padding: 0;
      text-align: center;
      font-size: 17px;
      font-weight: 600;
      letter-spacing: 0.4px;

      @include media('>phone') {
        text-align: left;
      }

      img {
        margin: 0 10px 0 0;
      }
    }

  }

  .address-holder, .working-hours {
    display: flex;
    align-items: flex-start;
    margin: 0 0 30px;
    font-size: 18px;
    line-height: 1.5;
    justify-content: center;

    @include media('>phone') {
      justify-content: flex-start;
    }

    .day {
      font-weight: 500;
    }

    /*.hour {
      font-size: 14px;
    }*/

    img {
      margin: 8px 15px 0 0;
    }
  }

  .address-holder {
    img {
      margin: 8px 13px 0 2px;
    }
  }

  .working-hours {
    align-items: center;
    img {
      width: 25px;
      margin: 0 15px 0 0;
    }
  }

  .inst-fb {
    max-width: 180px;
    align-items: center;
    justify-content: center;
  }

  .contact-form {
    input {
      width: 100%;
      height: 50px;
      border-radius: 25px;
      background: #F5F5F5;
      border: none;
      outline: none;
      padding: 10px 20px;
      font-size: 15px;
    }

    input:focus+.fake-placeholder {
      transform: translateY(-150%);
      font-size: 12px;
    }

    label {
      position: relative;
      display: block;
      margin: 0 0 20px;
    }


    .fake-placeholder {
      color: #929292;
      font-size: 15px;
      font-weight: 600;
      position: absolute;
      left: 20px;
      top: 50%;
      transform: translateY(-50%);
      transition: transform 0.3s, font-size 0.3s;
      pointer-events: none;

      &.to_top {
        transform: translateY(-150%);
        font-size: 12px;
      }
    }

    .btn {
      margin: 0 auto;
      @include media('>phone') {
        margin: 0;
      }
    }
  }

  .map-holder {
    height: 440px;
    iframe {
      width: 100%;
      height: 100%;
    }
  }
}

.inst-fb {
  display: flex;
  align-items: center;
  flex-wrap: wrap;
  justify-content: center;

  @include media('>phone') {
    justify-content: flex-start;
  }



  li {
   /* width: 100%;*/
    text-align: left;
    margin: 0 0 15px;
    display: flex;
    justify-content: center;

    @include media('>phone') {
      /*width: 50%;
      display: block;*/
    }

    a {
      display: flex;
      align-items: center;
      font-size: 16px;

      img {
        margin: 0 10px;
      }
    }
  }
}

