.header {
  min-height: 70px;
  display: flex;
  align-items: center;
  width: 100%;
  background: #fff;
  z-index: 999;
  top:-115px;
  left: 0;
  -webkit-transition:0.5s all ease;
  -moz-transition:0.5s all ease;
  transition:0.5s all ease;

  &.sticky {
    position: fixed;
    top: 0;
    width: 100%;
    /*background: rgba(0,0,0,0.7);*/
    color:#fff;
    padding: 3px;
    box-shadow:0 0 10px rgba(0,0,0,0.3);
  }
}

.mobile-header {
  display: flex;
  justify-content: space-between;
  align-items: center;

  @include media('>tablet') {
    display: none;
  }

}

.hamburger-holder {
  display: block;

  .hamburger {
    position: relative;
    width: 30px;
    height: 20px;
    cursor: pointer;
    /*&.active {
      .line1 {
        transform: translate(-50%, -50%), rotate(45deg);
      }

      .line3 {
        transform: translate(-50%, -50%) rotate(-45deg);
      }
    }*/

    .line {
      position: absolute;
      left: 50%;
      transform: translate(-50%, -50%);
      width: 100%;
      height: 2px;
      background: #000;
      transition: transform 0.3s;
    }

    .line1 {
      top: 0;
    }

    .line2 {
      top: 50%;
    }
    .line3 {
      top: 100%;
    }

    &.active {
      .line1 {
        transform: translate(-50%, -50%) rotate(45deg);
        top: 50%;
      }

      .line2 {
        display: none;
      }

      .line3 {
        transform: translate(-50%, -50%) rotate(-45deg);
        top: 50%;
      }
    }
  }
}

.header-holder {
  display: none;
  padding: 20px;
  position: absolute;
  left: 0;
  width: 100%;

  @include media('>tablet') {
    position: relative;
    left: auto;
    top: auto;
    display: flex;
    align-content: center;
    justify-content: space-between;
    padding: 0;
  }

  &.active {
    display: block;
    z-index: 999;
    background: #fff;
    height: 100vh;
  }

  .logo{
    display: none;
    @include media('>tablet') {
      display: flex;
    }
  }

  .social_list {
    li {
      padding: 5px 10px;
      color: #20d07f;
    }

    .header-phone {
      font-size: 18px;
      font-weight: 600;
      letter-spacing: 0.5px;

      @include media('>tablet') {
        font-size: 18px;
      }
      img {
        margin: 0 10px 0 0;
      }
    }
  }

}

.logo {
  display: flex;
  align-items: center;

  strong {
    padding: 0 0 0 10px;
  }
}

.menu-holder {
  display: flex;
  align-items: center;
}

.menu {
  display: block;
  width: 100%;

  @include media('>tablet') {
    display: flex;
  }
  li {

    a {
      display: block;
      text-transform: uppercase;
      font-size: 14px;
      font-weight: 600;
      letter-spacing: 0.5px;
      color: #333 !important;


      padding: 15px 13px;

      @include media('>tablet'){
        transition: color 0.3s;
        padding: 0 13px;
      }

      &:hover {
        color: #FFA300 !important;
      }
    }
  }
}

.contacts {
  display: flex;
  align-items: center;
  font-size: 16px;
  padding: 10px 0;

  @include media('>tablet') {
    padding: 0;
  }
}

.social_list {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  li {
    padding: 5px;

    a {
      opacity: 1;
      transition: opacity 0.3s;
      &:hover {
        opacity: 0.5;
      }
    }
  }

  .mobile-style {
    width: 100%;
    @include media('>tablet') {
      width: auto;
    }
  }
}